<template>
    <div class="login-app app-container" >
			
			<banner-component :swiper=swiper v-if="swiper"></banner-component>

			<div class="c-login">
				<img src="../assets/img/404.png" class="l-404" alt="">
				<p style="padding-bottom: 10px;">您尚未登录，无法浏览相关内容</p>
				<form action="" id="login-form">
					<input type="text" class="l-input l-username" name="account" ref="account" placeholder="请输入您的账号/手机号">
					<input type="password" class="l-input l-password" name="password" ref="password" onclick="this.value = null" placeholder="请输入您的密码">
					<p class="l-tips"></p>
					<div class="l-btn-group">
						<a href="#" target="_blank" class="l-register">注册</a>
						<a href="javascript:;" v-on:click="doLogin()" class="l-btn">登陆</a>
					</div>
				</form>
				<p class="l-line">或</p>
				<div class="l-link" id="l-link">
					<a :href="item.url || 'javascript:;'" target="_blank" v-for="item in thirdLinks" ><img :src="ApiConfig.domain+item.icon" :alt="item.party"></a>
				</div>
			</div>
		</div>
</template>

<script>
import Vue from "vue";
import BannerComponent from "@/components/BannerComponent.vue";

export default {
  name: "LoginPage",
  data() {
    return {
			swiper: null,
			thirdLinks: null,
			switch_on: true,
    };
	},
	methods:{
		doLogin(){
			 if(!this.switch_on){
            return false;
        }
				this.switch_on = false;  // 防止重复发送
				
				this.$store.commit('loading',true);

				this.$refs.password.value = $.md5(this.$refs.password.value)
				this.$store.dispatch('doLogin',{
						account:this.$refs.account.value,
						password: this.$refs.password.value
					}).then(({data}) => {
					if(data.success == "true"){
							window.location.href = this.ApiConfig.domain + data.url;
					}else{
							$('.l-tips').text(data.msg).show();
					}
					
					this.$store.commit('loading',false);
					this.switch_on = true;
				});
		}
	},
  mounted: function() {

		this.$store.dispatch('getBanner').then(({data}) => {
        this.swiper = data;
		});
		
		this.$store.dispatch('getThirdUrl').then(({data}) => {
        this.thirdLinks = data.ThirdpartyLogin;
		});
		
  },
  components: {
    BannerComponent
  }
};
</script>




