<template>
    <div class="tab-chat">
        <!-- 弹幕内容 Start -->
        <chat-msg-component :chat="chat" :gifts="gifts"></chat-msg-component>
        <!-- 弹幕内容 End -->

        <!-- 宝箱 Start -->
        <box-component @lotteryShowJson="lotteryShowJson"></box-component>
        <!-- 宝箱 End -->

        <!-- 输入框 Start -->
        <div class="chat-bar" id="chat-bar">
            <a href="javascript:;" class="chat-back bg-tab_back"></a>
            <input type="text" class="chat-input" id="chat-input" ref="chat-input" v-on:click="bar.focus = true" v-on:blur="blurBar()" placeholder="吐个槽呗~">
            <div class="chat-send" id="chat-send">
                <a href="" class="chat-more bg-tab_more" v-show="!bar.focus"></a>
                <div class="chat-submit" v-show="bar.focus">发送</div>
            </div>
            <a href="javascript:;" class="bg-tab_gifts" v-on:click="gift.active = true"></a>
        </div>
        <!-- 输入框 Start -->

        <gift-component v-show="gift.active" :data="gift.data" v-if="gift.data.length" v-on:close-gift-window="gift.active = false"></gift-component>

        <confirm-component :data="confirmObj" v-if="confirmObj.type" @close="confirmObj = null" @confirm="confirm"></confirm-component>

    </div>
</template>
<script>
import GiftComponent from '@/components/GiftComponent.vue'
import ChatMsgComponent from '@/components/ChatMsgComponent.vue'
import BoxComponent from '@/components/BoxComponent.vue'
import ConfirmComponent from '@/components/ConfirmComponent'

import {mapMutations, mapState, mapGetters} from 'vuex'

export default {
    data(){
        return {
            gift:{
                active: false,
                data: {}
            },
            bar:{
                focus: false
            },
            confirmObj: null
        }
    },
    computed:{
        ...mapState({
            chnid: state => state.BaseInfoService.chnid,
            baseInfo: state => state.BaseInfoService.baseInfo,

            chat: state => state.HeartBeanService.chat,
            gifts: state => state.HeartBeanService.gifts,
        })
    },
    methods:{
        closeGiftWindow(){
            this.gift.active = false;
        },
        blurBar(){
            if(!this.$refs['chat-input'].value.trim()){
                this.bar.focus = false;
            }
        },
        lotteryShowJson(data){
            if(data.success == 'success'){
                this.confirmObj = data;
            }else{
                this.$store.commit('alert',{
                    text: data.msg
                })
            }
        },
        confirm(){
            console.log('confirm')
        }
    },
    components:{
        GiftComponent,
        ChatMsgComponent,
        BoxComponent,
        ConfirmComponent
    },
    mounted: function(){
        this.$nextTick(function () {
            this.$store.dispatch('getGiftListJson',{
                chnid: this.chnid
            }).then(({data})=>{
                this.gift.data = data;
            })
        })
    }
}
</script>




