<template>
    <div class="c-tab" id="c-tab">
        <div class="tab-nav">
            <div :class="['tab-nav-item',tab.index == 0 ? 'active':'']" v-on:click="tab_click(0)"><span>互动交流</span></div>
            <div :class="['tab-nav-item',tab.index == 1 ? 'active':'']" v-on:click="tab_click(1)"><span>频道介绍</span></div>
            <div :class="['tab-nav-item',tab.index == 2 ? 'active':'']" v-on:click="tab_click(2)"><span>排行榜</span></div>
        </div>
        <div class="tab-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide">
                    <chat-component :chnid="chnid"></chat-component>
                </div>
                <div class="swiper-slide" >
                    <rich-text-component :data="baseInfo.intr" v-if="baseInfo.intr"></rich-text-component>
                </div>
                <div class="swiper-slide">
                    <rank-component :data="rank"></rank-component>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
import Swiper from 'swiper';

import ChatComponent from '@/components/ChatComponent.vue'
import RankComponent from '@/components/RankComponent.vue'
import RichTextComponent from '@/components/RichTextComponent.vue'

import {mapMutations, mapState, mapGetters} from 'vuex'


export default {
    name: "TabComponent",
    data(){
        return {
            tab:{
                index: 0,
                tab_container: null,
            },
            rank: null,
        }
    },
    components:{
        ChatComponent,
        RankComponent,
        RichTextComponent
    },
    computed:{
        ...mapState({
            chnid: state => state.BaseInfoService.chnid,
            baseInfo: state => state.BaseInfoService.baseInfo,
        })
    },
    methods:{
        tab_change(index){
            this.tab.index = index;
        },
        tab_click(index){
            this.tab_change(index);
            this.tab.tab_container.slideTo(index, 500, false);//切换到第一个slide，速度为1秒
        }
    },
    mounted: function(){
        this.$nextTick(() => {
            var _this = this;
            
            this.tab.tab_container = new Swiper('.tab-container', {
                on: {
                    slideChangeTransitionStart: function(){
                        if (_this.isAndroid){
                            $('#chat-input').blur();
                        }
                        _this.tab_change(this.activeIndex)
                    },
                },
                observer:true,      //修改swiper自己或子元素时，自动初始化swiper
                observeParents:true,//修改swiper的父元素时，自动初始化swiper
            });

            this.$store.dispatch('getRankingJson',{
                chnid: this.chnid
            }).then(({data})=>{
                this.rank = data;
            })
        });
    }
}
</script>



