<template>
    <div class="chat-window">
        <div class="chat-code">
            <p v-if="data.title">{{data.title}}</p>
            <input type="text" class="chat-input" v-if="data.type == 2" placeholder="请输入您的暗号">
            <div class="chat-confirm"><button @click="confirm">确认</button></div>
            <span class="chat-window-close" @click="close">×</span>
        </div>
    </div>
</template>

<script>
export default {
    props:['data'],
    methods:{
        close(){
            this.$emit('close')
        },
        confirm(){
            this.$emit('confirm')
        }
    }
}
</script>




