<template>
    <div class="tab-rich-text" id="tab-rich-text" >
        <a :href="item.link == 'undefined' || item.link == '' ? 'javascript:;': item.link"  v-for="item in JSON.parse(data)" style="color:#333">
            <img :src="ApiConfig.domain + item.img "  v-if="item.img" width="100%" style="display:block" alt="">
            <p v-if="item.text" v-text="item.text"></p>
        </a>
    </div>
</template>
<script>
export default {
    name: 'RichTextComponent',
    props: ['data'],
}
</script>



