
export default {
    state:{
        onlineIds:[],
        chat:[
            // {
            //     sender:'222222222',
            //     userlever: 22,
            //     viplevel: 18,
            //     content: new Date(),
            //     stamp: 1527080970,
            // },{
            //     sender:'222222222',
            //     userlever: 22,
            //     viplevel: 18,
            //     content: new Date() + new Date(),
            //     stamp: 1527080970,
            // }
        ],
        gifts:[
            // {
            //     sender:'小明',
            //     userlever: 22,
            //     viplevel: 18,
            //     reciver: '小红',
            //     gift: '超级火箭',
            //     quantity: 5 + Math.round(Math.random()*(20-5)),
            //     stamp: 1527080910,
            // },
            // {
            //     sender:'小明',
            //     userlever: 22,
            //     viplevel: 18,
            //     reciver: '小红',
            //     gift: '超级火箭',
            //     quantity: 5 + Math.round(Math.random()*(20-5)),
            //     stamp: 1527080920,
            // },
        ],
        //  公告队列
        notifys: [

        ],
        //  抽奖箱子
        boxes: [

        ]
    },
    getters:{
        firstNotice: (state) => {
            return state.notifys[0] || null;  
        },
        onlinIDArr: (state) => {
            const arr = [];
            state.onlineIds.map((data)=>{
                arr.push(data.value)
            })
            return arr;
        },
        getOnlineIdWithName: (state) => (name) =>{
            var id = null;
            state.onlineIds.forEach((data)=>{
                if(data.name == name){ id = data.value}
            })
            return id;
        },
        boxes: (state) => {
            return state.boxes.sort((a,b)=>{
                return a.enroll_b - b.enroll_b;
            })
        },
    },
    mutations:{
        pushOnlineId(state,params){
            state.onlineIds.push(params);
        },
        removeOnlineId(state,id){
            state.onlineIds = state.onlineIds.filter((data)=>{
                return data.value != id
            })
        },
        addChat(state,data){
            state.chat.push(data);
            //  清除过多的弹幕节点
            if(state.chat.length > 100){
                state.chat.splice(0,30);
            }
        },
        addGifts(state,data){
            state.gifts.push(data);
            //  清除过多的礼物节点
            if(state.gifts.length > 100){
                state.gifts.splice(0,30);
            }
        },
        addNotify(state,data){
            state.notifys = state.notifys.concat(data);
            state.notifys.sort((a,b) => {   //  排序
                return a.stamp - b.stamp;
            }); 
        },
        removeNotify(state){
            state.notifys.shift();  // 弹出队头
        },
        clearBoxes(state){  //  清除过期宝箱
            let now = Math.floor(new Date().getTime()/1000);
            const arr = [];
            state.boxes.map((box)=>{
                if(box.draw > now){
                    arr.push(box);
                }
            })
            state.boxes = arr;
        },
        addBoxes(state,data){
            this.commit('clearBoxes');
            data.forEach((item) => {
                if(state.boxes.every((box)=>{
                    return box.id != item.id
                })){
                    state.boxes.push(item);
                }
            })
        }
    }
}


